header{
  flex: 0 0 $header-height;
  position: relative;  
  z-index: z-index(high);
}

.header-logo{
  @extend %sprite;
  @include sprite($ic-logo-top);
}

.header-wrap{
  @include fixed(0, 0, null, 0);
  height: 60px;
  display: flex;
  justify-content: space-between;
  h1{
    @include poslt($primary-space, $default-space);
    font-size: 0;
  }
  .user{
    font-size: 0;
    @include posrt($primary-space, $default-space);
  }
  .user-name{
    @include vtext(30px);
    font-size: $font-size-base;
    position: relative;
    padding-left: 38px;
    margin: 0 $primary-space 0 $default-space;
    &:before{
      content:'';
      @extend %sprite;
      @include sprite($ic-man);
      @include poslt;
    }
  }
}

.gnb{
  font-size: 0;
  width: 240px * 3;
  margin: 0 auto;
  > li{
    display: inline-flex;
    > a{
      @include vtext(60px, $width: 240px);
      text-align: center;
      font-family: $semi-bold;
      font-size: 18px;
      color: $primary-black;
      position: relative;
      &:before{
        content:'';
        @include size(100%, 4px);
        @include poslb;
        background-color: color(black ,4);
      }
      &:after{
        content:'';
        @include absolute(null, 50%, 0, 50%);
        height: 4px;
        background-color: $blue;
        will-change: left, right;
        transition: left .25s ease, right .25s ease;
      }
      &:hover,
      &.active,
      &.active-current{
        color: $blue;
        &:after{
          left: 0;
          right: 0;
        }
        + .depth2-wrap{
          display: block;
        }
      }
    }
  }
  li{
    width: 240px;
  }
}

.depth2-wrap{
  @include poslt(0, $header-height);
  @include size(100%, $header-height);
  display: none;
  background-color: $blue;
  .depth2{
    width: 240px * 3;
    margin: 0 auto;
    > li{
      display: inline-block;
      vertical-align: top;
    }
  }
  .depth3-wrap{
    height: 60px;
    overflow: hidden;
    will-change: height;
    transition: height .3s ease;
    &.active{
      overflow: visible;
      > a{
        background-color: $primary-black;
      }
    }
    > a{
      @include vtext(60px, $width: 100%);
      color: $white;
      font-family: $semi-bold;
      font-size: 18px;
      text-align: center;
    }
  }
  .depth3{
    background-color: $primary-black;
    > li{
      position: relative;
      a{
        display: block;
        font-size: 14px;
        color: $white;
        padding: 7px 20px 8px;
        &.has-subdepth{
          position: relative;
          &:after{
            content:'';
            @extend %sprite;
            @include sprite($ic-menu-close);
            @include posrt(10px, 50%);
            margin: -2px 0 0 -3px;
            transform: rotate(90deg);
            transition: transform .2s ease;
          }
        }
        &:hover,
        &.active{
          background-color: $blue;
          &.has-subdepth{
            &:after{
              transform: rotate(0);
            }
          }
          + .depth4{
            display: block;
          }
        }
      }
      .depth4{
        @include poslt(100%, 0);
        display: none;
        background-color: $primary-black;
        a{
          &:hover,
          &:active{
            background-color: $blue;
          }
        }
      }
    }
  }
}