.login-wrap{
  @include size(100%);
  background: url('#{$img-url}/img-login-bg.png') no-repeat 50% 0;
  overflow: hidden;
}

.login-container{
  width: 1084px;
  margin: 0 auto;
  position: relative;
}

.login-text{
  margin-top: 170px;
  color: color(white, 2);
  span{
    font-family: $thin;
    font-size: 48px;
  }
  p{
    font-size: 32px;
    letter-spacing: -.8px;
  }
}

.login-image{
  margin: 82px 0 0 35px;
}

.login-input{
  @include posrt(0, 90px);
  width: 460px;
  background-color: $white;
  text-align: center;
  padding: 0 55px 20px;
  overflow: hidden;
  .user-thumbnail{
    @extend %sprite;
    @include size(102px);
    @include sprite($ic-login-man);
    margin: 40px auto 0;
  }
  p{
    font-size: 16px;
    color: color(black, 6);
    margin: 35px 0 15px 0;
  }
  input{
    @include vtext(50px);
    padding: 0 $primary-space;
  }
  input[type="password"]{
    margin-top: $default-space;
  }
  .btn-login{
    margin-top: $primary-space;
  }
  .find-id-pw{
    @include vtext(18px);
    color: color(black, 6);
    padding-left: 26px;
    position: relative;
    margin-top: 17px;
    &:before{
      content:'';
      @extend %sprite;
      @include sprite($ic-ligon-help);
      @include poslt;
    }
  }
}