.map-area{
  @include absolute(0, 0, 0, 0);
  img{
    @include size(100%);
  }
}

.left-panel{
  @include fixed($header-height, null, 0, -380px);
  width: 380px;
  display: flex;
  flex-direction: column;
  background-color: $white;
  transition: left .3s ease;
  will-change: left;
  &.history{
    top: 120px;
  }
  @include inherit('left-open'){
    left: 0;
  }
  @include inherit('pop-map'){
    top:0;
    left: 0;
  }
  .panel-title{
    @extend %bold;
    @include vtext(60px);
    color: $white;
  }
  .status-information{
    background-color: $primary-black;
    padding: 0 $primary-space ;
    .flex-box{
      padding-bottom:$primary-space ;
      .title{
        font-family: $semi-bold;
        color: $white;
        margin-right: $default-space;
      }
      .btn.blue{
        flex: 1 0 auto;
      }
    }
  }
  .status-number{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: $primary-space;
    > div{
      @include radius(4px);
      flex:0 0 165px;
      height: 60px;
      color: $white;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 0 15px;
      font-family: $semi-bold;
      span{
        font-size: 14px;
      }
      strong{
        font-size: 24px;
      }
      &.total{
        background-color: $blue;
      }
      &.violator{
        background-color: $red;
      }
    }
  }
  .mCSB_outside + .mCS-minimal-dark.mCSB_scrollTools_vertical{
    right: -17px;
  }
  .mCustomScrollbar{
    border: 0;
  }
  .thead th,
  .tbody td{
    padding: 9px $default-space;
  }
}

.btn-folding{
  @include size(20px, 54px);
  @include posrt(-20px, 50%);
  @include radius(0 4px 4px 0);
  margin-top: -27px;
  background-color: $white;
  &:after{
    content:'';
    @extend %sprite;
    @include sprite($ic-panel-open);
    @include poslt(50%, 50%);
    margin:-7px 0 0 -4px;
    will-change: transform;
    transition: transform .2s ease;
    transform: rotate(0);
  }
  @include inherit('left-open'){
    &:after{
      transform: rotate(180deg)
    }
  }
}

.panel-search{
  flex: 0 0 130px;
  border-bottom: 1px solid color(black, 9);
  padding: $primary-space;
}

.input-box{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  ~ .input-box{
    margin-top: $default-space;
  }
  input,
  select,
  .calendar{
    flex: 1 0 auto;
  }
  .btn{
    margin-left: $default-space;
  }
  .type{
    margin-right: $default-space;
  }
  .btn-search{
    flex: 0 0 40px;
    margin-left: $default-space;
  }
}

.search-result-title{
  flex: 0 0 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 $primary-space;
  border-bottom: 1px solid color(black, 9);
  font-size: 14px;
  color: $black;
  strong{
    @extend %bold;
    margin-right: 5px;
  }
}

.result-list{
  li ~ li{
    margin-top: $default-space;
  }
  a{
    @include bd(color(black, 7));
    display: block;
    dt{
      @extend %bold;
      @include vtext(40px);
      font-size: 14px;
      background-color: color(white, 2);
      padding-left: 18px;
      position: relative;
      &:after{
        content:'';
        @include size(4px, 100%);
        @include poslt(-1px, 0);
      }
      &.red:after{
        background-color: $red;
      }
      &.green:after{
        background-color: color(green, 2);
      }
      &.orange:after{
        background-color: color(red, 2);
      }
      &.yellow:after{
        background-color: color(yellow, 1);
      }
    }
    dd{
      color: color(black, 6);
      padding: 6px $default-space;
      p{
        font-family: $semi-bold;
        margin-bottom: 4px;
      }
    }
  }
}
.flex-box{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn-modify-area{
  padding: $primary-space;
  text-align: right;
  font-size: 0;
  border-bottom: 1px solid color(black, 9);
}

.inner-content{
  display:flex;
  flex-direction: column;
  flex: 1 0 auto;
  padding: $primary-space;
  .btn-area{
    flex: 0 0 40px;
    text-align: right;
    margin-bottom: $default-space;
  }
}