table{
  width: 100%;
  border-spacing: 0;
  table-layout: fixed;
  text-align: center;
  font-size: 13px;
  line-height: 1.3;
  border-top: 1px solid $table-border-top;
  &.tbody{
    border-top: 0;
  }
  thead{
    th{
      font-family: $semi-bold;
      height: $thead-height;
      padding: $thead-padding;
      background-color: $thead-background-color;
      border-bottom: 1px solid $tbody-border-color;
      color: color(black, 6);
      + th{
        border-left: 1px solid $thead-border-color;
      }
    }
  }
  tbody{
    td{
      height: $tbody-height;
      padding: $tbody-padding;
      border-bottom: 1px solid $tbody-border-color;
      + td{
        border-left: 1px solid $tbody-border-color;
      }
      &.input{
        text-align: center;
        font-size: 0;
      }
    }
  }
}

.scroll-flex-wrap{
  flex: 1 0 auto;
  position: relative;
  .scroll-area{
    @include absolute(0, 0, 0, 0);
  }
}

.mCustomScrollbar{
  @include bd($border-color-base, 0 1px 1px 1px);
  &.scroll-end{
    border-bottom-color: transparent;
  }
  &.mCS_no_scrollbar{
    border: 0;
  }
}