button{
  @include radius(0);
  cursor: pointer;
  overflow: hidden;
  outline: none;
  position: relative;
  span{
    position: relative;
    vertical-align: top;
    &.hidden{
      @include size(100%);
      overflow: hidden;
      color: transparent;
    }
  }
}

a,
button {
  display: inline-block;
  vertical-align: middle;
  border: 0 none;
  font: inherit;
  background: none;
  color: inherit;
}

a,
a:active,
a:hover,
a:focus{
  text-decoration: none;
}

.btn{
  @include vtext(40px, 38px);
  @include radius(4px);
  font-family: $semi-bold;
  min-width: 80px;
  padding: 0 $primary-space;
  text-align: center;
  font-size: 14px;
  &.black{
    @include bd($primary-black);
    background-color: $primary-black;
    color: $white;
  }
  &.blue{
    @include bd($blue);
    background-color: $blue;
    color: $white;
  }
  &.white{
    @include bd($primary-black);
    background-color: $white;
    color: color(black, 6);
    &.in-table{
      border-color: $primary-blue;
      color: $primary-blue;
    }
  }
  &.big{
    @include vtext(50px, 48px);
    width: 200px;
  }
  ~ .btn{
    margin-left: $default-space;
  }
}

.btn-wrap{
  text-align: right;
  margin-top: 30px;
  font-size: 0;
}

.btn-logout{
  @include size(40px);
  @include radius(4px);
  background-color: $primary-black;
  text-align: center;
  &:after{
    content:'';
    @extend %sprite;
    @include sprite($ic-logout);
    @include poslt(5px, 5px);
  }
}

.btn-login{
  @extend %bold;
  @include radius(6px);
  @include vtext(60px);
  display:block;
  width: 100%;
  background-color: $blue;
  color: $white;
  font-size: 18px;
}

.btn-search{
  @include size(40px);
  @include radius(4px);
  background-color: $blue;
  text-align: center;
  &:after{
    content:'';
    @extend %sprite;
    @include sprite($ic-searchs);
    @include poslt(11px, 11px);
  }
}

.btn-refresh{
  @extend %sprite;
  @include sprite($ic-refresh);
}

.btn-calendar{
  @extend %sprite;
  @include sprite($ic-calendar);
}

.btn-radius{
  @include radius(4px);
  @include bd(color(black, 10));
  @include vtext(40px, 38px, 40px);
  background-color: $blue;
  text-align: center;
  font-family: $semi-bold;
  font-size: 14px;
  color: $white;
}

.btn-request-alart{
  @include size(30px);
  @include radius(50%);
  background-color: color(red, 2);
  position: relative;
  &:before{
    content:'';
    @extend %sprite;
    @include sprite($ic-signal-white);
    @include poslt(5px, 5px);
  }
}