.gnb {
  font-size: 0;
  width: 720px;
  margin: 0 auto;
}

.gnb > li {
  display: inline-flex;
}

.gnb > li > a {
  height: 60px;
  line-height: 60px;
  width: 240px;
  text-align: center;
  font-family: "Noto-medium", sans-serif;
  font-size: 18px;
  color: #434759;
  position: relative;
}

.gnb > li > a:before {
  content: '';
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #e5e5e5;
}

.gnb > li > a:after {
  content: '';
  position: absolute;
  right: 50%;
  bottom: 0;
  left: 50%;
  height: 4px;
  background-color: #24aae1;
  will-change: left, right;
  transition: left .25s ease, right .25s ease;
}

.gnb > li > a:hover, .gnb > li > a.active, .gnb > li > a.active-current {
  color: #24aae1;
}

.gnb > li > a:hover:after, .gnb > li > a.active:after, .gnb > li > a.active-current:after {
  left: 0;
  right: 0;
}

.gnb > li > a:hover + .depth2-wrap, .gnb > li > a.active + .depth2-wrap, .gnb > li > a.active-current + .depth2-wrap {
  display: block;
}

.gnb li {
  width: 240px;
}

.depth2-wrap {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  height: 60px;
  display: none;
  background-color: #24aae1;
}

.depth2-wrap .depth2 {
  width: 720px;
  margin: 0 auto;
}

.depth2-wrap .depth2 > li {
  display: inline-block;
  vertical-align: top;
}

.depth2-wrap .depth3-wrap {
  height: 60px;
  overflow: hidden;
  will-change: height;
  transition: height .3s ease;
}

.depth2-wrap .depth3-wrap.active {
  overflow: visible;
}

.depth2-wrap .depth3-wrap.active > a {
  background-color: #434759;
}

.depth2-wrap .depth3-wrap > a {
  height: 60px;
  line-height: 60px;
  width: 100%;
  color: #fff;
  font-family: "Noto-medium", sans-serif;
  font-size: 18px;
  text-align: center;
}

.depth2-wrap .depth3 {
  background-color: #434759;
}

.depth2-wrap .depth3 > li {
  position: relative;
}

.depth2-wrap .depth3 > li a {
  display: block;
  font-size: 14px;
  color: #fff;
  padding: 7px 20px 8px;
}

.depth2-wrap .depth3 > li a.has-subdepth {
  position: relative;
}

.depth2-wrap .depth3 > li a.has-subdepth:after {
  content: '';
  background-position: -112px -98px;
  width: 6px;
  height: 3px;
  position: absolute;
  top: 50%;
  right: 10px;
  margin: -2px 0 0 -3px;
  transform: rotate(90deg);
  transition: transform .2s ease;
}

.depth2-wrap .depth3 > li a:hover, .depth2-wrap .depth3 > li a.active {
  background-color: #24aae1;
}

.depth2-wrap .depth3 > li a:hover.has-subdepth:after, .depth2-wrap .depth3 > li a.active.has-subdepth:after {
  transform: rotate(0);
}

.depth2-wrap .depth3 > li a:hover + .depth4, .depth2-wrap .depth3 > li a.active + .depth4 {
  display: block;
}

.depth2-wrap .depth3 > li .depth4 {
  position: absolute;
  top: 0;
  left: 100%;
  display: none;
  background-color: #434759;
}

.depth2-wrap .depth3 > li .depth4 a:hover, .depth2-wrap .depth3 > li .depth4 a:active {
  background-color: #24aae1;
}

