$palettes: (
	white: (
		1: #fff,
		2: #f5f5f5,
		3: #fbfbfb,
		4: #f8f8f8,
		5: #f5f6f9,
	),
	black: (
		1: #000,
		2: #222,
		3: #434759,
		4: #e5e5e5,
		5: #434759,
		6: #666,
		7: #ddd,
		8: #888,
		9: #eee,
		10: #242737,
	),
	red: (
		1: #ef2a23,
		2: #f47723,
	),
	green: (
		1: #14692b,
		2: #30a736,
	),
	blue: (
		1: #24aae1,
		2: #337ab7,
		3: #041047,
		4: #0e62bc,
	),
	yellow: (
		1: #ffd140,
	)
);

// color
$white: color(white, 1);
$black: color(black, 1);
$red: color(red, 1);
$green: color(green, 1);
$blue: color(blue, 1);
$background-color-base: color(white, 2);
$primary-black: color(black, 3);
$primary-blue: color(blue, 2);

//reset 폰트 관련
$font-color-base: color(black, 2) !default;
$font-size-base: 13px !default;
$font-family-base: 'Noto-regular', sans-serif !default;
$semi-bold: "Noto-medium", sans-serif;
$bold: "Noto-bold", sans-serif;
$thin: "Noto-thin", sans-serif;
$content-center-size: 1200px !default;

// 이미지 경로 관련 variable
// $img-url : "../../dist/images/sprite";
$img-url : "/dist/images";
$sprite : "#{$img-url}/sprite/img_sprite.png";
$img-retina : "#{$img-url}/sprite/retina.png";
$img-retina2x : "#{$img-url}/sprite/retina2x.png";

//border basic style - border mixin 관련
$border-color-base: color(black, 4) !default;
$border-style-base: solid !default;
$border-width-base: 1px !default;

// input status
$input-placeholder-font-color: color(black, 8);
$input-disable-background-color: color(white, 4);
$input-disable-border-color: $border-color-base;
$input-disable-font-color: $black;
$input-space: 10px;

// 공통 여백 사이즈
$default-space: 10px;
$primary-space: 20px;

$header-height: 60px;
$footer-height: 80px;

// paging variable
$paging-height: null;
$paging-top-space: 50px;
$paging-active-color: $blue;
$paging-prev-space: $default-space;
$paging-next-space: $default-space;
$first-image: null;
$prev-image: null;
$next-image: null;
$last-image: null;

// table variable
$table-font-size: 13px;
$table-border-top: $primary-black;
$thead-height: 40px;
$thead-padding: 20px;
$thead-border-color: color(black, 7);
$thead-background-color: color(white, 3);
$tbody-height: 40px;
$tbody-padding: 20px;
$tbody-border-color: color(black, 7);

// route2
$sqrt2 : 1.41421356237;

// z-index 매핑
$z-indexes: (
	zero				: 0,
	low					: 5,
	mid					: 10,
	high				: 15,
	higher			: 20,
	dim         : 150,
	modal       : 200
);
