.layer-pop{
  @include center-position(470px);
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.7);
  background-color: $white;
  z-index: z-index(modal);
  display: none;
  &.arrow{
    &:before{
      content:'';
      @include poslb(50%, -12px);
      margin-left: -12px;
      border-left: 12px solid transparent;
      border-right: 12px solid transparent;
      border-top: 13px solid $white;
      z-index:1;
    }
  }

  .layer-title{
    @extend %bold;
    @include vtext(60px, 59px);
    border-bottom: 1px solid color(black, 7);
    padding: 0 $primary-space;
    background-color: $primary-black;
    color: $white;
    text-transform: uppercase;
    font-size: 16px;
    &.white{
      background-color: $white;
      color: $primary-black;
    }
  }
  
  .flex-box{
    justify-content: end;
    color: color(black, 6);
    .title{
      width: 98px;
    }
    input[type="text"].range{
      width: 100px;
      margin-right: $default-space;
    }
    textarea{
      flex: 1 0 auto;
      width: auto;
    }
    &:last-child{
      padding-bottom: $primary-space;
    }
    ~ .flex-box{
      margin-top: $default-space;
    }
  }
  
  .layer-container{
    padding: $primary-space $primary-space 0;
  }

  .btn-wrap{
    margin-top: 0;
    padding: $primary-space 0;
    text-align: center;
    border-top: 1px solid color(black, 7);
  }
  
}

.validation-area{
  @include vtext(40px);
  text-align: center;
  color: $primary-blue;
  strong{
    @extend %bold;
  }
}

input[type="text"]{
  + .input-with-btn{
    margin-top: $default-space;
  }
}

.input-with-btn{
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  input{
    flex: 1 0 auto;
  }
  .btn{
    flex: 0 0 97px;
    margin-left: 10px;
  }
}

.layer-close{
  @extend %sprite;
  @include sprite($ic-pop-close-w);
  @include posrt(20px, 18px);
}