.radius{
  position: absolute;
  .btn-radius{
    @include poslt(50%, 50%);
    z-index: 5;
    margin: -20px 0 0 -20px;
  }
  .radius-range{
    @include size(180px);
    @include radius(50%);
    @include bd($white, 2px);
    @include center-position;
    background-color: rgba(36, 170, 225, 0.1);
    z-index: 4;
  }
}

.radius-setting{
  position: absolute;
  .btn.blue{
    display: flex;
    white-space: nowrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-color: color(black, 10);
    span{
      font-family: $font-family-base;
      margin-left: 13px;
    }
  }
  .layer-pop{
    width: 460px;
    top: auto;
    bottom:-65px;
  }
}

.alarm-status{
  @include posrt($primary-space, $primary-space);
  font-size: 0;
  > div{
    @include radius(4px);
    @include vtext(50px, 48px);
    display: inline-block;
    vertical-align: top;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.7);
    padding: 0 10px 0 50px;
    position: relative;
    color: $white;
    font-size: 16px;
    &:before{
      content:'';
      @include radius(50%);
      @include size(30px);
      @include poslt(10px, 10px);
      background-color: $white;
    }
    &:after{
      content:'';
      @extend %sprite;
      @include poslt(15px, 15px);
    }
    ~ div{
      margin-left: $default-space;
    }
    &.alarm{
      @extend %bold;
      background-color: $red;
      &:after{
        @include sprite($ic-signal);
      }
    }
    &.time{
      background-color: $primary-black;
      &:after{
        @include sprite($ic-day);
      }
    }
  }
}

.location-info-wrap{
  position: absolute;
}

.ic-location-red{
  @extend %sprite;
  @include sprite($ic-map-location);
}

.ic-location-green{
  @extend %sprite;
  @include sprite($ic-map-normal);
}

.ic-location-orange{
  @extend %sprite;
  @include sprite($ic-map-electricity);
}

.ic-location-yellow{
  @extend %sprite;
  @include sprite($ic-map-breakage);
}

.layer-map-info{
  @include poslb(50%, 65px);
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.7);
  margin-left: -230px;
  width: 460px;
  &:before{
    content:'';
    @include poslb(50%, -15px);
    margin-left: -14px;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-top: 15px solid $white;
    z-index:1;
  }
  .map-info-title{
    height: 60px;
    color: $white;
    padding: 2px $primary-space 0;
    strong{
      @extend %bold;
      text-transform: uppercase;
      font-size: 16px;
    }
    p{
      font-family: $font-family-base;
      font-size: $font-size-base;
    }
    &.red{
      background-color: $red;
    }
    &.green{
      background-color: color(green, 2);
    }
    &.orange{
      background-color: color(red, 2);
    }
    &.yellow{
      background-color: color(yellow, 1);
    }
  }
  .map-layer-container{
    padding: $primary-space $primary-space $primary-space 140px;
    background-color: $white;
    position: relative;
    min-height: 165px;
    .user-thumb{
      @include poslt($primary-space, $primary-space);
    }
    .user-name-no{
      font-family: $semi-bold;
      color: color(black, 2);
    }
    .user-etc-info{
      margin-top: 10px;
      li{
        display: inline-flex;
        flex-direction: row;
        flex-wrap: wrap;
        span{
          color: $black;
          &:first-child{
            width: 175px;
            color: color(black, 6);
            padding-left: $default-space;
            position: relative;
            &:before{
              content:'';
              @extend %sprite;
              @include sprite($blit-circle);
              @include poslt(0, 9px);
            }
          }
        }
        ~ li{
          margin-top: 3px;
        }
      }
    }
  }
}

.map-flag-wrap{
  position:absolute;
}

.ic-flag-red{
  @extend %sprite;
  @include sprite($ic-map-location-s);
}

.ic-flag-green{
  @extend %sprite;
  @include sprite($ic-map-normal-s);
}

.ic-flag-orange{
  @extend %sprite;
  @include sprite($ic-map-electricity-s);
}

.ic-flag-yellow{
  @extend %sprite;
  @include sprite($ic-map-breakage-s);
}

.map-layer-alert{
  @include poslb(50%, 65px);
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.7);
  margin-left: -110px;
  width: 220px;
  background-color: $white;
  &:before{
    content:'';
    @include poslb(50%, -15px);
    margin-left: -14px;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-top: 15px solid $white;
    z-index:1;
  }
  .event-title{
    display: table;
    width: 100%;
    table-layout:fixed;
    background-color: color(white, 2);
    span{
      display: table-cell;
      height: 40px;
      text-align: center;
      font-family: $semi-bold;
      font-size: 13px;
      color: $primary-black;
      padding: 5px $primary-space 7px;
    }
  }
  .alert-container{
    padding: 6px $primary-space 15px;
    text-align: center;
    .date{
      color: color(black, 2);
      font-size: 14px;
    }
    .caution{
      font-family: $semi-bold;
      color: $red;
    }
    .btn{
      margin-top: 10px;
    }
  }
}