label{
  position: relative;
  display: inline-block;
  cursor: pointer;
  vertical-align: middle;
}

input[type=text]::-ms-clear {
  display: none !important;
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: $input-placeholder-font-color !important;
}
::-moz-placeholder { /* Firefox 19+ */
  color: $input-placeholder-font-color !important;
}
:-ms-input-placeholder { /* IE 10+ */
  color: $input-placeholder-font-color !important;
}

input[type="text"]:disabled,
select:disabled,
input[readonly="readonly"],
textarea[readonly="readonly"]{
  border-color: $input-disable-border-color;
  background-color: $input-disable-background-color;
  color: $input-disable-font-color;
}

%input-figure{
  @include bd;
  @include vtext(40px);
  @include radius(4px);
  font-family: $font-family-base;
  font-size: $font-size-base;
  color: $font-color-base;
  font-size: $font-size-base;
}

input[type="text"],
input[type="password"]{
  @extend %input-figure;
  padding: 0 $default-space;
}

select{
  @extend %input-figure;
  padding: 0 $default-space;
}

input[type="file"]{
  @include size(1px);
  @include poslt;
  opacity: 0;
}

input[type="checkbox"],
input[type="radio"]{
  @include absolute;
  opacity: 0;
}

textarea{
  @include radius(4px);
  @include bd(color(black, 7));
  @include size(100%, 50px);
  display: block;
  padding: $default-space;
  margin:0;
  font-size: $font-size-base;
  font-family: $font-family-base;
  color: $font-color-base;
  resize: none;
  box-sizing: border-box;
}

input[type="text"],
input[type="password"],
textarea{
  outline:none;
}


