.calendar{
  @extend %input-figure;
  position: relative;
  padding: 0 38px 0 $default-space;
  input{
    @include vtext(38px);
    vertical-align: top;
    border: 0;
    width: 100%;
    padding: 0;
  }
  .btn-calendar{
    @include posrt(10px, 10px);
  }
